import { Component, OnDestroy, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { Package } from '@imunify360-api/license';
import { AuthState } from 'app/services/auth-state';
import { AppState } from 'app/core/app.service';
import { AgentStatus } from '@imunify360-api/misc';
import { combineLatestWith, takeUntil, Subject } from 'rxjs';


@Component({
    selector: 'installation-tabs',
    templateUrl: './installation-tabs.component.html',
    styleUrls: ['./installation-tabs.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class InstallationTabsComponent implements OnDestroy {
    showTabs: boolean = false;
    isUpgrading: boolean = false;

    private destroy$ = new Subject<void>();

    constructor(cdr: ChangeDetectorRef, authState: AuthState, appState: AppState) {
        authState.isAdmin.pipe(
            combineLatestWith(appState.imunifyStatus),
            takeUntil(this.destroy$),
        ).subscribe(([isAdmin, imunifyStatus]) => {
            const is360 = IMUNIFY_PACKAGE === Package.imunify360;
            this.isUpgrading = imunifyStatus === AgentStatus.UPGRADING;
            this.showTabs = (is360 || this.isUpgrading) && isAdmin;

            cdr.markForCheck();
        });
    }

    ngOnDestroy() {
        this.destroy$.next();
    }
}
