import { Component, ViewEncapsulation, OnDestroy, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { Package } from '@imunify360-api/license';
import { AgentStatus } from '@imunify360-api/misc';
import { AppState } from 'app/core/app.service';

import { Router } from '@angular/router';
import { useLoader } from 'app/utils/loader/decorators';
import { InstallationService } from 'app/components/installation/installation.service';
import { takeUntil, Subject } from 'rxjs';


@Component({
    selector: 'installation-header',
    templateUrl: './installation-header.component.html',
    styleUrls: ['./installation-header.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class InstallationHeaderComponent implements OnDestroy {
    isAv = IMUNIFY_PACKAGE === Package.imunifyAV;
    AgentStatus = AgentStatus;
    imunifyStatus: AgentStatus;
    isUpgrading= false;
    isInstalling = false;

    private destroy$ = new Subject<void>();

    constructor(
        private cdr: ChangeDetectorRef,
        public appState: AppState,
        private router: Router,
        private installationService: InstallationService,
    ) {
        this.listenImunifyStatus();
    }

    ngOnDestroy(): void {
        this.destroy$.next();
    }

    @useLoader
    async handleRetryBtnClick() {
        // this line fixes should cancel log request if retry button was clicked
        this.installationService.stopWatchingLog();
        await this.installationService.start();
        if (this.router.url === '/installation/progress/log') {
            this.installationService.startWatchingLog();
        }
    }

    private listenImunifyStatus(): void {
        this.appState.imunifyStatus
            .pipe(takeUntil(this.destroy$))
            .subscribe(imunifyStatus => {
                this.imunifyStatus = imunifyStatus;
                this.isInstalling = imunifyStatus === AgentStatus.INSTALLING;
                this.isUpgrading = imunifyStatus === AgentStatus.UPGRADING;
                this.cdr.markForCheck();
            });
    }
}
