import { Component, ChangeDetectionStrategy, OnDestroy } from '@angular/core';
import { timer, switchMap, tap, catchError, BehaviorSubject, Subject, takeUntil, repeat } from 'rxjs';
import { LogChunk } from 'app/utils/log-box/log-box.component';
import { InstallationStatus } from '@imunify360-api/installation';
import { ExtendedRequestParams, DataFrom, AgentResponse } from '@imunify360-api/misc';
import { XHR } from 'app/utils/xhr/xhr';
import { LOG_CHUNK_SIZE } from 'app/components/installation/installation.service';

const LOG_REFRESH_INTERVAL = 2000;

@Component({
    selector: 'upgrade-log',
    templateUrl: './upgrade-log.component.html',
    styleUrls: ['./upgrade-log.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
})
export class UpgradeLogComponent implements OnDestroy {
    accumulatedLog = new BehaviorSubject<LogChunk[]>([]);

    private offset = 0;
    private destroy$ = new Subject<void>();

    constructor(private xhr: XHR) {
        this.startPollingUpgradeStatus();
    }

    ngOnDestroy() {
        this.destroy$.next();
    }

    private startPollingUpgradeStatus(): void {
        timer(0).pipe(
            switchMap(() => this.upgradeStatus({
                offset: this.offset,
                limit: LOG_CHUNK_SIZE,
            })),
            tap(res => {
                const items = res.data.items;
                this.offset = items.offset;
                const chunk = items.log;
                if (chunk) {
                    this.accumulatedLog.next(this.accumulatedLog.getValue().concat([{text: chunk}]));
                }
            }),
            catchError(() => timer(LOG_REFRESH_INTERVAL)),
            repeat({ delay: LOG_REFRESH_INTERVAL }),
            takeUntil(this.destroy$),
        ).subscribe();
    }

    private upgradeStatus({offset, limit}: {offset: number, limit: number}) {
        const statusApi = ({offset, limit}: {offset: number, limit: number}) => <ExtendedRequestParams<
            DataFrom<AgentResponse<{items: InstallationStatus}>>>> {
            method: ['upgrading', 'status'],
            params: { offset, limit },
        };

        return this.xhr.rx(statusApi, false, false)({ offset, limit });
    }
}
